

#editorCV {
  padding: 2rem;
}

input {
  margin-top: 0.8rem;
  border: none;
  background-color: rgb(218, 220, 230);
  border-radius: 2px;
  height: 2rem;
  padding-left: 10px;
}
form{
    width: 50%;
}
input:focus {
  outline-width: 0;
  border-bottom: 1px solid rgb(0, 162, 255);
}

/* personal details */
h3 {
  color: rgb(58, 58, 58);
}
#PersonalInfo {
  align-items: end;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr) 2fr;
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
}
.textareaLabel {
  width: fit-content;
  grid-area: 4 / 1 / 4 / 3;
  width: 100%;
}
.textareaCV {
  resize: none;
  overflow-y: scroll;
  width: 100%;
  padding: 0.5rem;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto';
}
#uploadPhotoDiv {
  display: flex;
  text-align: center;
  align-items: center;
}

#styleUploadBtnLabel {
  background-color: rgb(82, 166, 221);
  color: white;
  font-family: sans-serif;
  padding: 5px;
  border-radius: 0.3rem;
  cursor: pointer;
  width: 2rem;
  text-align: center;
  margin-right: 1rem;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.281);
}
#styleUploadBtnLabe:hover {
  color: black;
  background-color: rgb(0, 217, 255);
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.281);
}
#photoInput {
  display: none;
}

.workExperienceForm {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2fr) 2fr;
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
}
.experienceForm{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2fr) 2fr;
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
}
label,
input {
  display: block;
}
label {
  color: rgb(83, 83, 83);
  font-size: 70%;
}


.experienceFormDiv{
    position: relative;
    width: fit-content
}
.deleteForm{
    background:transparent;
    border: none;
    position: absolute;
        top: -1rem;
        right: -1rem;
}
.addNewField{
    margin-top: 0.4rem;
    background: transparent;
    border:none;
    font-family: 'Roboto';
    font-size: 90%;
    white-space: nowrap;
    color: rgb(1, 123, 204);
    text-decoration: underline;
    padding: 0.3rem 1rem 0.3rem 0.5rem;
    border-radius: 5px;
}
.addNewField:hover{
    background-color: rgba(218, 220, 230, 0.637);
}


#skillsForm {
    flex-direction: row;
    justify-content: flex-start;
}
#skillsFormInput{
    width: 100%;
    height: 2rem;
    box-sizing: border-box;
    margin: 0;
    padding: 0.5rem;
    font-family: 'Roboto';
}
#skillsForm button{
    margin-top: 0;
    font-weight: 500;
}

#skillList{
    display: flex;
    list-style: none;
}
.skillListItem{
    white-space: nowrap;
   display: flex;
   align-items: center;
   background-color: rgb(68, 163, 218);
   color: white;
   margin: 0.3rem;
   padding: 2px 6px;

   font-size: 0.9rem;
   border-radius: 20px;
}

#hideViewBtn{
    position: fixed;
    top: 2rem;
    right: 5rem;

    color:rgb(255, 255, 255);   
    text-shadow: 2px 2px 10px black; 
    z-index: 10;
    transition:color 1s;
    transition-delay: 0.2s;
}

#hideViewBtn:hover{
    position: fixed;
    top: 2rem;
    right: 5rem;

    color:rgb(158, 227, 255);   
    text-shadow: 2px 2px 10px black; 
    z-index: 10;
    transition:color 1s;
    transition-delay: 0.2s;
}

#hideViewBtn.onWhite{

    color:rgb(58, 70, 87)
}


#loadExampleDiv{
  display: flex;
  justify-content: center;
}

#loadExampleBtn{
  background-color:#ff893b;
  color:white;
  font-family: 'Roboto';
  font-weight: 700;
  border-radius: 4px;
  box-shadow: 1px 1px 5px darkslategray;
  height: 1.5rem;
  position: absolute;
  margin-bottom: 1rem;
}